/**
 * RISIVO CRM - Demo Self-Signup API
 * 
 * Allows investors to sign up for demo access themselves.
 * Flow:
 * 1. POST /api/demo/signup - Submit signup form
 * 2. Verification email sent with secure link
 * 3. User clicks link to verify email
 * 4. User sets password
 * 5. Demo access activated
 */

import { NextResponse } from 'next/server'
import { prisma } from '@/lib/db'
import { DEMO_SETTINGS } from '@/lib/auth/constants'
import { sendAdminNotification, getAdminNewSignupEmail } from '@/lib/email/sendgrid'
import crypto from 'crypto'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

// Generate a secure random token
function generateToken(): string {
  return crypto.randomBytes(32).toString('hex')
}

// Get dynamic copyright year
function getCurrentYear(): number {
  return new Date().getFullYear()
}

// Generate email HTML template
function getVerificationEmailHtml(name: string, verificationLink: string): string {
  return `
    <!DOCTYPE html>
    <html>
    <head>
      <meta charset="utf-8">
      <meta name="viewport" content="width=device-width, initial-scale=1.0">
    </head>
    <body style="font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif; line-height: 1.6; color: #333; max-width: 600px; margin: 0 auto; padding: 20px;">
      <div style="text-align: center; margin-bottom: 30px;">
        <img src="https://app.risivo.com/risivo-logo-color.png" alt="RISIVO CRM" style="height: 40px;">
      </div>
      
      <h1 style="color: #1a1a1a; font-size: 24px; margin-bottom: 20px;">Welcome to RISIVO CRM, ${name}!</h1>
      
      <p style="margin-bottom: 20px;">Thank you for signing up for a demo of RISIVO CRM. Please verify your email address to complete your registration and access the demo.</p>
      
      <div style="text-align: center; margin: 30px 0;">
        <a href="${verificationLink}" style="display: inline-block; background-color: #2563eb; color: white; padding: 14px 28px; text-decoration: none; border-radius: 8px; font-weight: 600;">Verify Email & Set Password</a>
      </div>
      
      <p style="color: #666; font-size: 14px;">This link will expire in 24 hours. If you didn't request this, you can safely ignore this email.</p>
      
      <p style="color: #666; font-size: 14px;">If the button doesn't work, copy and paste this link into your browser:</p>
      <p style="color: #2563eb; font-size: 12px; word-break: break-all;">${verificationLink}</p>
      
      <div style="margin-top: 30px; padding-top: 20px; border-top: 1px solid #e9ecef;">
        <p style="color: #999; font-size: 12px; text-align: center; margin: 0 0 10px 0;">
          <a href="https://risivo.com/terms-of-service" style="color: #666; text-decoration: none;">Terms of Service</a>
          &nbsp;|&nbsp;
          <a href="https://risivo.com/privacy-policy" style="color: #666; text-decoration: none;">Privacy Policy</a>
          &nbsp;|&nbsp;
          <a href="mailto:support@risivo.com" style="color: #666; text-decoration: none;">Contact Support</a>
        </p>
        <p style="color: #999; font-size: 11px; text-align: center; margin: 0;">
          © ${getCurrentYear()} Risivo™ by Velocity Automation Corp. All rights reserved.
        </p>
      </div>
    </body>
    </html>
  `
}

// Send verification email using SendGrid or fallback
async function sendVerificationEmail(email: string, name: string, token: string): Promise<boolean> {
  const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://app.risivo.com'
  const verificationLink = `${baseUrl}/demo/verify?token=${token}`
  
  // Check if SendGrid is configured (primary email service)
  const sendgridApiKey = process.env.SENDGRID_API_KEY
  
  if (!sendgridApiKey) {
    // Log the verification link for testing when no email service is configured
    console.log('========================================')
    console.log('DEMO VERIFICATION EMAIL (No email service configured)')
    console.log('To:', email)
    console.log('Name:', name)
    console.log('Verification Link:', verificationLink)
    console.log('========================================')
    return true // Return true so the flow continues
  }
  
  // Send via SendGrid
  try {
    const response = await fetch('https://api.sendgrid.com/v3/mail/send', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${sendgridApiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        personalizations: [
          {
            to: [{ email, name }],
            subject: 'Verify your RISIVO CRM Demo Access',
          },
        ],
        from: {
          email: process.env.SENDGRID_FROM_EMAIL || 'hello@risivo.com',
          name: process.env.SENDGRID_FROM_NAME || 'RISIVO CRM',
        },
        content: [
          {
            type: 'text/html',
            value: getVerificationEmailHtml(name, verificationLink),
          },
        ],
        tracking_settings: {
          click_tracking: { enable: false },
          open_tracking: { enable: false },
        },
      }),
    })
    
    if (!response.ok) {
      const errorText = await response.text()
      console.error('[Demo Signup] Failed to send verification email via SendGrid:', response.status, errorText)
      return false
    }
    
    console.log('[Demo Signup] Verification email sent successfully to:', email)
    return true
  } catch (error) {
    console.error('[Demo Signup] Error sending verification email:', error)
    return false
  }
}

export async function POST(request: Request) {
  console.log('[Demo Signup] Request received')
  
  // Check for test mode via query parameter (only works when email not configured)
  const url = new URL(request.url)
  const testMode = url.searchParams.get('test') === 'true'
  
  try {
    const body = await request.json()
    const { name, email, company, phone } = body

    // Validate required fields
    if (!name || !email || !company) {
      return NextResponse.json(
        { success: false, error: 'Name, email, and company are required' },
        { status: 400 }
      )
    }

    // Validate email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (!emailRegex.test(email)) {
      return NextResponse.json(
        { success: false, error: 'Invalid email format' },
        { status: 400 }
      )
    }

    // Normalize email
    const normalizedEmail = email.toLowerCase().trim()

    if (!prisma) {
      return NextResponse.json(
        { success: false, error: 'Database not available' },
        { status: 503 }
      )
    }

    // Check if email already exists
    const existingDemo = await prisma.demoAccess.findUnique({
      where: { email: normalizedEmail },
    })

    if (existingDemo) {
      // If already verified and active, tell them to log in
      if (existingDemo.emailVerified && existingDemo.status === 'active') {
        return NextResponse.json(
          { success: false, error: 'An account with this email already exists. Please log in instead.' },
          { status: 409 }
        )
      }
      
      // If pending verification, resend the email
      if (!existingDemo.emailVerified || existingDemo.status === 'pending_verification') {
        // Generate new verification token
        const verificationToken = generateToken()
        
        await prisma.demoAccess.update({
          where: { id: existingDemo.id },
          data: {
            verificationToken,
            verificationSentAt: new Date(),
            name, // Update name in case they changed it
            company,
            phone,
          },
        })
        
        // Send verification email
        await sendVerificationEmail(normalizedEmail, name, verificationToken)
        
        return NextResponse.json({
          success: true,
          message: 'A verification email has been sent. Please check your inbox.',
          isResend: true,
        })
      }
      
      // If expired or revoked, allow re-registration
      if (existingDemo.status === 'expired' || existingDemo.status === 'revoked') {
        const verificationToken = generateToken()
        const expiresAt = new Date()
        expiresAt.setDate(expiresAt.getDate() + DEMO_SETTINGS.EXPIRY_DAYS)
        
        await prisma.demoAccess.update({
          where: { id: existingDemo.id },
          data: {
            name,
            company,
            phone,
            verificationToken,
            verificationSentAt: new Date(),
            emailVerified: false,
            password: null,
            status: 'pending_verification',
            expiresAt,
            signupSource: 'self_signup',
          },
        })
        
        await sendVerificationEmail(normalizedEmail, name, verificationToken)
        
        return NextResponse.json({
          success: true,
          message: 'A verification email has been sent. Please check your inbox.',
        })
      }
    }

    // Create new demo access
    const verificationToken = generateToken()
    const expiresAt = new Date()
    expiresAt.setDate(expiresAt.getDate() + DEMO_SETTINGS.EXPIRY_DAYS)

    const demoAccess = await prisma.demoAccess.create({
      data: {
        name,
        email: normalizedEmail,
        company,
        phone,
        verificationToken,
        verificationSentAt: new Date(),
        status: 'pending_verification',
        expiresAt,
        signupSource: 'self_signup',
      },
    })

    console.log('[Demo Signup] Created demo access:', demoAccess.id)

    // Send verification email
    const emailSent = await sendVerificationEmail(normalizedEmail, name, verificationToken)
    
    if (!emailSent) {
      console.error('[Demo Signup] Failed to send verification email')
      // Don't fail the request, but log it
    }

    // Send admin notification
    const adminEmail = getAdminNewSignupEmail({
      name,
      email: normalizedEmail,
      company,
      phone,
      signupTime: new Date(),
    })
    
    try {
      const adminEmailSent = await sendAdminNotification(adminEmail)
      if (adminEmailSent) {
        console.log('[Demo Signup] Admin notification sent successfully')
      } else {
        console.error('[Demo Signup] Admin notification failed to send')
      }
    } catch (err) {
      console.error('[Demo Signup] Failed to send admin notification:', err)
    }

    // Build verification URL
    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://app.risivo.com'
    const verificationUrl = `${baseUrl}/demo/verify?token=${verificationToken}`

    // Check if email service is configured (SendGrid)
    const emailConfigured = !!process.env.SENDGRID_API_KEY
    
    // Return verification URL if:
    // 1. Test mode is enabled via query param, OR
    // 2. No email service is configured
    const returnVerificationUrl = testMode || !emailConfigured

    return NextResponse.json({
      success: true,
      message: emailConfigured 
        ? 'Thank you for signing up! Please check your email to verify your account.'
        : 'Thank you for signing up! Email service not configured - use the verification URL below.',
      // Include verification URL when in test mode or no email configured
      ...(returnVerificationUrl && { 
        verificationUrl,
        _note: 'Use this URL to verify your email and set your password' 
      }),
    })

  } catch (error: any) {
    console.error('[Demo Signup] Error:', error)
    
    // Handle unique constraint violation
    if (error.code === 'P2002') {
      return NextResponse.json(
        { success: false, error: 'An account with this email already exists.' },
        { status: 409 }
      )
    }
    
    return NextResponse.json(
      { success: false, error: 'Failed to process signup. Please try again.' },
      { status: 500 }
    )
  }
}
